// import logo from './logo.svg';
import './App.css';
import axios from "axios";
import useState from 'react-hook-use-state';


function App() {
  const [persons, setPersons] = useState([]);
  const [i, setI] = useState(1);
  if (i === 1) {
    axios.get(`https://zocoly-backend.herokuapp.com/product/get/`)
      .then(res => {
        // const persons = res.data;
        setPersons(res.data[0])
        console.log(persons)
      })
    setI(2)
  }
  return (
    <div className="App">
      <h1>{persons.id}</h1>
      <h1>{persons.description}</h1>
      <h1>{persons.name}</h1>
      <img src={persons.length !== 0 ? persons.files[0].image : ''} />
      <h1>{persons.length !== 0 ? persons.collections[0].name : ''}</h1>
      <h1>{persons.length !== 0 ? persons.collections[1].name : ''}</h1>
    </div>
  );
}

export default App;
